<template>
  <div class="d-flex flex-column flex-root">
    <div
      class="login login-1 d-flex flex-column flex-lg-row flex-column-fluid bg-white"
      :class="{
        'login-signin-on': this.state == 'signin',
        'login-signup-on': this.state == 'signup',
        'login-forgot-on': this.state == 'forgot',
      }"
      id="kt_login"
    >
      <!--begin::Content-->
      <div class="login-content flex-row-fluid d-flex flex-column justify-content-center position-relative overflow-hidden p-7 mx-auto">
        <div class="d-flex flex-column-fluid flex-center">
          <!--begin::Signin-->
          <div class="login-form login-signin">
            <form class="form" novalidate="novalidate" id="kt_login_signin_form" @submit.stop.prevent="onSubmitLogin()">
              <div class="pb-13 pt-lg-0 pt-5">
                <h3 class="font-weight-bolder text-dark font-size-h4 font-size-h1-lg">
                  Login ke MarketWatch
                </h3>
                <span class="text-muted font-weight-bold font-size-h4">Baru? <a id="kt_login_signup" class="text-primary font-weight-bolder" @click="showForm('signup')">Silahkan mendaftar</a></span>
              </div>
              <div class="form-group">
                <label class="font-size-h6 font-weight-bolder text-dark">Username</label>
                <div id="example-input-group-1" label="" label-for="example-input-1">
                  <input class="form-control form-control-solid h-auto py-7 px-6 rounded-lg" type="text" name="username" v-model="username" readonly="readonly" />
                </div>
              </div>
              <div class="form-group">
                <div class="d-flex justify-content-between mt-n5">
                  <label class="font-size-h6 font-weight-bolder text-dark pt-5">Login Token</label>
                </div>
                <div id="example-input-group-2" label="" label-for="example-input-2">
                  <input class="form-control form-control-solid h-auto py-7 px-6 rounded-lg" type="login_token" name="login_token" v-model="login_token" autocomplete="off" />
                </div>
              </div>
              <div class="pb-lg-0 pb-5">
                <button ref="kt_login_signin_submit" class="btn btn-primary font-weight-bolder font-size-h6 px-15 py-4 my-3 mr-3">
                  Sign In
                </button>
              </div>
            </form>
          </div>
          <!--end::Signin-->
          <!--begin::Signup-->
          <div class="login-form login-signup">
            <form class="form" novalidate="novalidate" id="kt_login_signup_form" @submit.stop.prevent="onSubmitRegister()">
              <div class="pb-13 pt-lg-0 pt-5">
                <h3 class="font-weight-bolder text-dark font-size-h4 font-size-h1-lg">
                  Mendaftar
                </h3>
                <p class="text-muted font-weight-bold font-size-h4">
                  Masukkan kode dari pak rokhim disini
                </p>
              </div>
              <div class="form-group">
                <textarea
                  rows="6"
                  class="form-control form-control-solid h-auto py-7 px-6 rounded-lg font-size-h6"
                  type="text"
                  placeholder="Register Hash"
                  v-model="register_token"
                  autocomplete="off"
                />
              </div>
              <div class="form-group d-flex flex-wrap pb-lg-0 pb-3">
                <button ref="kt_login_signup_submit" class="btn btn-primary font-weight-bolder font-size-h6 px-8 py-4 my-3 mr-4" style="width:150px;">
                  Register
                </button>
                <button type="button" class="btn btn-light-danger font-weight-bolder font-size-h6 px-8 py-4 my-3 mr-4" @click="unRegister()">
                  Unreg
                </button>
                <button type="button" id="kt_login_signup_cancel" class="btn btn-light-primary font-weight-bolder font-size-h6 px-8 py-4 my-3" @click="showForm('signin')">
                  Cancel
                </button>
              </div>
            </form>
          </div>
          <!--end::Signup-->
        </div>
        <!--begin::Content footer-->
        <!--end::Content footer-->
      </div>
      <!--end::Content-->
    </div>
  </div>
</template>

<!-- Load login custom page styles -->
<style lang="scss">
@import "@/assets/sass/pages/login/login-1.scss";
</style>

<script>
import KTUtil from "@/assets/js/components/util";
import { mapGetters } from "vuex";
import { LOGIN, REGISTER, UNREGISTER } from "@/core/services/store/auth.module";
import Swal from "sweetalert2";
// import hmacSHA512 from "crypto-js/hmac-sha512"

export default {
  name: "login-1",
  data() {
    return {
      state: "signin",
      // Remove this dummy login info
      username: "",
      login_token: "",
      register_token: localStorage.getItem("mw_register_hash"),
    };
  },
  computed: {
    ...mapGetters(["currentUser", "isAuthenticated", "cekLoginToken"]),

    backgroundImage() {
      return process.env.BASE_URL + "media/svg/illustrations/login-visual-1.svg";
    },
  },
  mounted() {
    let logintoken = this.cekLoginToken;
    // console.log("login precheck1", logintoken.status == "ok");
    // console.log("login precheck2", logintoken.expired > Date.now());
    // console.log("login precheck3", logintoken);
    if (logintoken.status == "ok" && logintoken.expired > Date.now()) {
      this.$router.push({ name: "dashboard" });
    } else {
      this.reloadForm();
    }
  },
  methods: {
    reloadForm() {
      this.register_token = localStorage.getItem("mw_register_hash");
      this.username = this.currentUser.username;
    },
    showForm(form) {
      this.state = form;
      var form_name = "kt_login_" + form + "_form";
      KTUtil.animateClass(KTUtil.getById(form_name), "animate__animated animate__backInUp");
    },

    onSubmitLogin() {
      // set spinner to submit button
      const submitButton = this.$refs["kt_login_signin_submit"];
      submitButton.classList.add("spinner", "spinner-light", "spinner-right");

      // send login request
      this.$store
        .dispatch(LOGIN, this.login_token)
        .then(login => {
          if (login.status != "ok") {
            Swal.fire({
              title: "Gagal!",
              text: login.message,
              icon: "error",
              confirmButtonClass: "btn btn-secondary",
              heightAuto: false,
            });
          } else {
            this.$router.push({ name: "dashboard" });
          }

          submitButton.classList.remove("spinner", "spinner-light", "spinner-right");
        })
        .catch(() => {});
    },

    onSubmitRegister() {
      // set spinner to submit button
      const submitButton = this.$refs["kt_login_signup_submit"];
      submitButton.classList.add("spinner", "spinner-light", "spinner-right");

      this.$store.dispatch(REGISTER, this.register_token).then(register => {
        submitButton.classList.remove("spinner", "spinner-light", "spinner-right");

        if (register.status != "ok") {
          Swal.fire({
            title: "Gagal!",
            text: register.message,
            icon: "error",
            confirmButtonClass: "btn btn-secondary",
            heightAuto: false,
          });
        } else {
          Swal.fire({
            title: "Sukses!",
            text: "Pendaftaran sukses, silahkan login",
            icon: "success",
            confirmButtonClass: "btn btn-secondary",
            heightAuto: false,
          }).then(() => {
            this.reloadForm();
            this.showForm("signin");
          });
        }
      });
    },
    unRegister() {
      this.$store.dispatch(UNREGISTER).then(() => {
        this.reloadForm();
        Swal.fire({
          title: "Berhasil unreg!",
          text: "Silahkan register kembali",
          icon: "success",
          confirmButtonClass: "btn btn-secondary",
          heightAuto: false,
        });
      });
    },
  },
};
</script>
